import { NextResponse } from 'next/server';import type { NextRequest } from 'next/server';export const config={matcher:['/dashboard/:path*','/items/new']};export function middleware(req:NextRequest){const cookie=process.env.IRON_SESSION_COOKIE_NAME||'givehub_session';if(!req.cookies.has(cookie)){return NextResponse.redirect(new URL('/signin',req.url));}return NextResponse.next();}